// Этот блок ответственен ТОЛЬКО за типографику проекта.
// Не пишите тут какую-либо стороннюю стилизацию.
$gray: hsl(0, 0%, 50%) !default;
$gray-lighter: hsl(0, 0%, 80%) !default;
$gray-lightest: hsl(0, 0%, 90%) !default;
$font-family: 'leroy_merlin_sansregular', 'Arial', sans-serif;
$font-family--headings: $font-family !default;
$font-family--light: 'leroy_merlin_sanslight', 'Arial', sans-serif;
$font-family--bold: 'leroy_merlin_sansbold', 'Arial', sans-serif;
$font-family--semi-bold: 'leroy_merlin_sanssemibold', 'Arial', sans-serif;
$font-size: 14px !default;
$font-size--h1: 2.25em !default;
$font-size--h2: 1.875em !default;
$font-size--h3: 1.5em !default;
$font-size--h4: 1.25em !default;
$font-size--h5: 1em !default;
$font-size--h6: 1em !default;
$font-size--small: 0.75em !default;
$line-height: 1.375em !default;
$text-color: hsl(0, 0%, 10%) !default;
$border-color: hsl(0, 0%, 60%) !default;
$screen-sm: 480px !default;
$border-radius: 3px !default;

