// В этом файле должны быть стили для БЭМ-блока offer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.offer {

  $block-name:                &; // #{$block-name}__element

  max-width: 840px;
  margin: auto;

  &__cost {
    font-size: 61px;
    line-height: 1.2;
    background: #c4d82d;
    border-radius: 55px;
    padding: 20px 60px;
    max-width: 470px;
    margin: 0 auto 25px auto;
    text-align: center;
  }

  &__caption {
    font-size: 20px;
    line-height: 1.2;
    text-align: center;
    margin-bottom: 55px;
  }

  &__sheme {
    text-align: center;
    margin-bottom: 30px;
  }

  &__nav {
    height: 220px;
  }

  &__action {
    text-align: center;
  }

}
